.card-header {
    padding: 1rem 1.25rem !important;
    margin-bottom: 0 !important;
    background-color: #268d61 !important;
    border-bottom: 0 solid #268d61 !important;
}
.card-widgets {
    float: right;
    height: 16px;
}
.bg-one, .bg-two, .bg-three, .bg-four {
  border-radius: .25rem;
  margin-bottom: 24px;
  color: #fff;
}
.bg-one h5, .bg-two h5, .bg-three h5, .bg-four h5 {
  margin-top: 2px !important;
}
.bg-one {
  background-image: linear-gradient(to right, #24BBB9 , #44E794);
}
.bg-two {
  background-image: linear-gradient(to right, #24A1BB , #44D1E7);
}
.bg-three {
  background-image: linear-gradient(to right, #44E794 , #24BBB9);
}
.bg-four {
  background-image: linear-gradient(to right, #44D1E7 , #24A1BB);
}
