.searchf .input-group-text {
  background: #fff;
  border-right: 0;
  padding: 0.375rem 0.55rem;
  font-size: 21px;
  color: #81868b;
  padding-right: 0px !important;
}
.searchf .form-control {
  border-left: 0;
  outline: none;
  box-shadow: none;
}
.searchf .input:focus {
  outline: none;
  box-shadow: none;
  border: 0px;
}
.searchf .form-control:focus {
  border-color: #ced4da;
  -webkit-box-shadow: none;
  box-shadow: none;
}
