}
.iselected {
  width: 200px;
  height: 60px;
  text-align: center;
  overflow: hidden;
}
.iselected .vi {
  margin: 13px 0px;
}
.iselected img {
  width: 105px;
}
#ileft img {
  width: 105px;
  display: block;
  cursor: move;
}
img + hr {
  background-color: #ededed !important;
  margin: 10px 0 !important;
  opacity: 1 !important;
}
#ileft hr {
  background-color: #fff;
  margin: 0px;
}
.iselected hr {
  display: none;
}
@media (max-width: 500px) {
  .inte .saw1 {
    display: none;
  }
  .inte .card-title {
    font-size: 12px;
  }
  .inte .card-body {
    padding: .5rem .3rem;
  }
  .card-header {
    padding: 1rem 0.3rem !important;
  }
  #ileft img {
    width: 50px;
  }
  .saw2 {
    min-width: 65px;
  }
}
