/* Menu Button */
.left-panel-btn {
  padding: 0 1.1rem 0 1.1rem;
}
.left-panel-btn .lines {
  width: 20px;
  display: block;
  position: relative;
  height: 13px;
  margin: 0 auto;
  transition: all 0.5s ease;
}
.left-panel-btn span {
  height: 2px;
  width: 100%;
  background-color: #a6b0cf;
  display: block;
  margin-bottom: 4px;
  -webkit-transition: -webkit-transform 0.5s ease;
  transition: -webkit-transform 0.5s ease;
  transition: transform 0.5s ease;
}

.sidebar-enable .left-panel-btn span {
  position: absolute;
}
.sidebar-enable .left-panel-btn span:first-child {
  top: 6px;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
}
.sidebar-enable .left-panel-btn span:nth-child(2) {
  visibility: hidden;
}
.sidebar-enable .left-panel-btn span:last-child {
  width: 100%;
  top: 6px;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

