/************** Horizontal timeline **************/
.hori-timeline .events .event-list {
  text-align: center;
  display: block;
}
.hori-timeline .events .event-list::after {
  content: "\e858";
  font-family: "unicons-line";
  display: block;
  position: absolute;
  top: 34px;
  left: 0;
  right: 0;
  margin: 0px auto;
  color: #ced4da;
  font-size: 24px;
}
.hori-timeline .events .event-list:before {
  content: "";
  display: block;
  position: absolute;
  top: 14px;
  left: 0;
  right: 0;
  width: 100%;
  height: 2px;
  border-radius: 50%;
  background-color: #dee2e6;
}
.hori-timeline .events .event-list .event-date {
  display: inline-block;
  position: relative;
  border-radius: 4px;
  background-color: rgba(13, 110, 253, 0.2);
  padding: 2px 12px;
  margin-bottom: 48px;
}

/************** vertical timeline **************/
.verti-timeline {
  padding-left: 100px;
}
.verti-timeline .event-list {
  border-left: 3px dashed #dee2e6;
  position: relative;
  padding: 0px 0px 30px 30px;
}
.verti-timeline .event-list:after {
  content: "";
  display: block;
  position: absolute;
  top: 6px;
  left: -6px;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid #0d6efd;
  background-color: #fff;
}
.verti-timeline .event-list .event-date {
  display: inline-block;
  border-radius: 4px;
  background-color: rgba(13, 110, 253, 0.2);
  color: #0d6efd;
  padding: 2px 12px;
  position: absolute;
  left: -102px;
}
.verti-timeline .event-list:last-child {
  padding-bottom: 0;
}

