// Used invoice page
@media print {
    .left-panel,
    .right-bar,
    .page-title-box,
    .nav-header,
    .footer {
        display: none !important;
    }
    .card-body,
    .content-body,
    .right-bar,
    .content,
    body {
        padding: 0;
        margin: 0;
    }

    .card{
        border: 0;
    }
}