/* ==============
  Email
===================*/
.email-card {
  background: $card-bg;
  margin-bottom: 24px;
  box-shadow: $box-shadow;
}

.email-menu {
  width: 260px;
  float: left;
  padding: 20px;
}

.email-right-box {
  width: 100%;
}

.email-action {
  padding: 20px 15px;

  .btn {
    &:focus {
      box-shadow: none!important;
    }
  }
}

.chat-user-box {
  p.user-title {
    color: $dark;
    font-weight: 500;
  }
  p {
    font-size: 13px;
  }
}

@media (max-width: 767px) {
  .email-menu {
    float: none;
    width: 100%;
  }
  .email-right-box {
    margin: 0;
  }
}


.mail-list {
  a {
    display: block;
    color: $body-color;
    line-height: 24px;
    padding: 5px 9px;
    margin-bottom: 6px;
    border-radius: $btn-border-radius;
    &.active {
      color: $danger;
      background: #fff1f1 !important;
      font-weight: 500;
    }
    &:hover {
      background: $light;
    }
  }
}

.message-list {
  display: block;
  padding-left: 0;

  li {
    position: relative;
    display: block;
    height: 46px;
    line-height: 46px;
    cursor: default;

    a{
      color: $gray-600;
    }

    &:hover {
      background: $gray-100;
    }

    .col-mail {
      float: left;
      position: relative;
    }

    .col-mail-1 {
      width: 300px;

      .star-toggle,
      .checkbox-wrapper-mail,
      .dot {
        display: block;
        float: left;
      }

      .dot {
        border: 4px solid transparent;
        border-radius: 100px;
        margin: 22px 26px 0;
        height: 0;
        width: 0;
        line-height: 0;
        font-size: 0;
      }

      .checkbox-wrapper-mail {
        margin: 13px 10px 0 15px;
      }

      .star-toggle {
        margin-top: 15px;
        margin-left: 5px;
      }

      .title {
        position: absolute;
        top: 0;
        left: 85px;
        right: 0;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
        margin-bottom: 0;
      }
    }

    .col-mail-2 {
      position: absolute;
      top: 0;
      left: 300px;
      right: 0;
      bottom: 0;

      .subject,
      .date {
        position: absolute;
        top: 0;
      }

      .subject {
        left: 0;
        right: 120px;
        text-overflow: ellipsis;
        overflow: hidden;
        white-space: nowrap;
      }

      .date {
        right: 0;
        width: 158px;
        padding-left: 80px;
      }
    }

    &.active,
    &.active:hover {
      box-shadow: inset 3px 0 0 $primary;
    }

    
  &.unread  {
    font-weight: 500;
    color: darken($dark,5%);
      a{
        color: darken($dark,5%);
        font-weight: 500;
      }
    }
  }


  .checkbox-wrapper-mail {
    cursor: pointer;
    height: 20px;
    width: 20px;
    position: relative;
    display: inline-block;
    box-shadow: inset 0 0 0 1px $gray-400;
    border-radius: 1px;

    input {
      opacity: 0;
      cursor: pointer;
    }
    input:checked ~ label {
      opacity: 1;
    }

    label {
      position: absolute;
      height: 20px;
      width: 20px;
      left: 0;
      cursor: pointer;
      opacity: 0;
      margin-bottom: 0;
      transition-duration: .05s;
      top: 0;
      &:before {
        content: "\F012C";
        font-family: "Material Design Icons";
        top: 0;
        height: 20px;
        color: darken($dark,5%);
        width: 20px;
        position: absolute;
        margin-top: -14px;
        left: 4px;
        font-size: 13px;
      }
    }
  }
}

@media (max-width: 575.98px) { 
  .message-list li .col-mail-1 {
      width: 200px;
  }
}