//
// _menu.scss
// 

.metismenu {
    margin: 0;

    li {
        display: block;
        width: 100%;
    }

    .mm-collapse {
        display: none;

        &:not(.mm-show) {
            display: none;
        }

        &.mm-show {
            display: block
        }
    }

    .mm-collapsing {
        position: relative;
        height: 0;
        overflow: hidden;
        transition-timing-function: ease;
        transition-duration: .35s;
        transition-property: height, visibility;
    }
}


.left-panel {
    width: $sidebar-width;
    z-index: 1005;
    background-image: url(../images/left-bg.png);
    background-size: 100% 100%;
    background-attachment: fixed;
    background-position: center;
    background-repeat: no-repeat;
    background-color: #ffffff;
    bottom: 0;
    margin-top: 0;
    position: fixed;
    top: 0;
    box-shadow: $box-shadow;
    transition-duration: .2s;

    .left-panel-btn {
        position: absolute;
        right: 0;
        top: 0;
        z-index: 2;
    }
}

.content-body {
    margin-left: $sidebar-width;
    overflow: hidden;
    transition-duration: .2s;

    .content {
        padding: 24px 15px 60px 15px;
        margin-top: $header-height;
    }
}

.main-menu-scroll {
    height: calc(100% - #{$header-height});
    margin-top: $header-height;
}


#main-menu {
    padding: 5px 0 14px 0;

    .mm-active {
        >.menu-arrow {
            &:after {
                transform: rotate(-180deg);
            }
        }
    }

    .menu-arrow {
        &:after {
            content: "\F0140";
            font-family: 'Material Design Icons';
            display: block;
            float: right;
            transition: transform .2s;
            font-size: 1rem;
        }
    }

    ul {
        padding-left: 0;
        list-style: none;

        li {
            a {
                display: block;
                padding: 13px 25px;
                margin: 3px 0;
                color: #cbebf7;
                position: relative;
                font-size: 15px;
                font-weight: 400;
                transition: all .4s;

                i {
                    display: inline-block;
                    min-width: 1.5rem;
                    padding-bottom: .125em;
                    font-size: 1.05rem;
                    line-height: 1.40625rem;
                    vertical-align: middle;
                    color: #cbebf7;
                    transition: all .4s;
                }

                &.active {
                    background: #0a85bf;
                }

                &:hover {
                    color: #fff;

                    i {
                        color: #fff;
                    }
                }
            }

            .badge {
                margin-top: 4px;
            }

            ul.submenu-content {
                padding: 0;

                li {

                    a {
                        padding: 11px 25px 10px 53px;
                        font-weight: 400;
                        color: $main-menu-sub-item-color;
                        margin: 0;
                        background-color: transparent;
                        font-size: 14.4px;

                        &:hover {
                            color: $main-menu-item-hover-color;
                        }
                    }

                    ul.submenu-content {
                        padding: 0;

                        li {
                            a {
                                padding: 11px 25px 10px 70px;
                                font-size: 13.5px;
                            }
                        }
                    }
                }
            }
        }

    }
}

.main-menu-title {
    padding: 12px 20px !important;
    letter-spacing: .035em;
    pointer-events: none;
    cursor: default;
    font-size: 13px;
    color: #cbebf7cf;
}

.mm-active {
    color: #00dbff !important;

    > a {
        color: #00dbff !important;  
        > i{
            color: #00dbff !important;
        }
    }

   
    .active {
        color: #fff !important;

        i {
            color: #fff !important;
        }
    }
    > i {
        color: $main-menu-item-active-color !important;
    }
}

@media (max-width: 992px) {
    .left-panel {
        display: none;
        top: $header-height;

        .main-menu-scroll {
            height: 100%;
            margin-top: 0;
        }

        .brand-logo,
        .left-panel-btn {
            display: none;
        }

    }

    .content-body {
        margin-left: 0 !important;
    }

    body.sidebar-enable {
        .left-panel {
            display: block;
        }
    }
}

// Enlarge menu
.vertical-collpsed {

    @media (min-width: 769px) {
        min-height: 1450px;
    }

    .content-body {
        margin-left: $sidebar-collapsed-width;
    }

    .brand-logo {
        width: $sidebar-collapsed-width !important;
    }

    .logo {
        span.logo-lg {
            display: none;
        }

        span.logo-sm {
            display: block;
        }
    }

    // Side menu
    .left-panel {
        position: absolute;
        width: $sidebar-collapsed-width !important;
        z-index: 1002;

        .simplebar-mask,
        .simplebar-content-wrapper {
            overflow: visible !important;
        }

        .simplebar-scrollbar,
        .left-panel-btn {
            display: none !important;
        }

        .simplebar-offset {
            bottom: 0 !important;
        }

        // Sidebar Menu
        #main-menu {

            .main-menu-title,
            .badge,
            .collapse.in {
                display: none !important;
            }

            .nav.collapse {
                height: inherit !important;
            }

            .menu-arrow {
                &:after {
                    display: none;
                }
            }

            > ul {
                > li {
                    position: relative;
                    white-space: nowrap;

                    > a {
                        padding: 15px 22px;
                        transition: none;
                        
                        &:hover,
                        &:active,
                        &:focus {
                            color: $main-menu-item-hover-color;
                        }

                        i {
                            font-size: 1.2rem;
                            text-align: center;
                            padding-bottom: 0;
                        }

                        span {
                            display: none;
                            padding-left: 25px;
                        }
                    }

                    &:hover {
                        > a {
                            transition: none;

                            i{
                                color: #fff;
                            }
                        }

                        >ul {
                            display: block;
                            left: 75px;
                            position: absolute;
                            top: 0;
                            width: 190px;
                            height: auto !important;
                            border-radius: $dropdown-border-radius;
                            box-shadow: 3px 1px 10px 0 rgba(56, 63, 74, .12);

                            ul {
                                box-shadow: 3px 1px 10px 0 rgba(56, 63, 74, .12);
                            }

                            a {
                                box-shadow: none;
                                padding: 8px 20px;
                                position: relative;
                                width: 190px;
                                z-index: 6;
                                color: $main-menu-sub-item-color;

                                &:hover {
                                    color: $main-menu-item-hover-color;
                                }
                            }

                            &:before {
                                content: "\f035e";
                                display: inline-block;
                                font: normal normal normal 24px/1 "Material Design Icons";
                                position: absolute;
                                left: -13px;
                                top: 13px;
                                color: #fff;
                            }
                        }
                    }
                }

                ul {
                    padding: 5px 0;
                    z-index: 9999;
                    display: none;
                    background-color: $sidebar-bg;

                    li {
                        &:hover {
                            >ul {
                                display: block;
                                left: 190px;
                                height: auto !important;
                                margin-top: -36px;
                                position: absolute;
                                width: 190px;
                            }
                        }

                        >a {
                            span.pull-right {
                                position: absolute;
                                right: 20px;
                                top: 12px;
                                transform: rotate(270deg);
                            }
                        }
                    }

                    li.active {
                        a {
                            color: $gray-100;
                        }
                    }
                }
            }
        }

    }
}


body[data-sidebar="dark"] {
    .left-panel {
        background: $sidebar-dark-bg;

        .left-panel-btn{
            color: $sidebar-dark-menu-item-color;
        }
    }

    #main-menu {
    
        ul {
            li {
                a {
                    color: $sidebar-dark-menu-item-color;

                    i {
                        color: $sidebar-dark-menu-item-icon-color;
                    }
    
                    &:hover {
                        color: $sidebar-dark-menu-item-hover-color;

                        i {
                            color: $sidebar-dark-menu-item-hover-color;
                        }
                    }
                }

                &:hover {
                    >ul {
                        &:before {
                            color: $sidebar-dark-bg !important;
                        }
                    }
                }

                ul.submenu-content {
                    li {

                        a {
                            color: $sidebar-dark-menu-sub-item-color;

                            &:hover {
                                color: $sidebar-dark-menu-item-hover-color;
                            }
                        }
                    }
                }
            }
        }
    }
    // Enlarge menu
    &.vertical-collpsed {
        min-height: 1300px;
        // Side menu
        .left-panel {

            // Sidebar Menu
            #main-menu {

                > ul {
                    > li {
                        
                        &:hover {
                            > a {
                                background: lighten($sidebar-dark-bg, 2%);
                                color: $sidebar-dark-menu-item-hover-color;
                                i{
                                    color: $sidebar-dark-menu-item-hover-color;
                                }
                            }

                            >ul {
                                a{
                                    color: $sidebar-dark-menu-sub-item-color;
                                    &:hover{
                                        color: $sidebar-dark-menu-item-hover-color;
                                    }
                                }
                            }
                        }
                    }

                    ul{
                        background-color: $sidebar-dark-bg;
                    }
                    
                }

                ul{
                    
                    li{
                        &.mm-active .active{
                            color: $main-menu-item-active-color !important;
                            i{
                                color: $main-menu-item-active-color !important;
                            }
                        }
                    }
                }

            }


        }
    }
    .mm-active {
        color: $sidebar-dark-menu-item-active-color !important;
        > i {
            color: $sidebar-dark-menu-item-active-color !important;
        }

        >a {
            background-color: $main-menu-item-active-bg-color-dark;
            color: $sidebar-dark-menu-item-active-color !important;
            >i{
                color: $sidebar-dark-menu-item-active-color !important;
            }
        }
        .active {
            color: $sidebar-dark-menu-item-active-color !important;

            i {
                color: $sidebar-dark-menu-item-active-color !important;
            }
        }
    }

    .main-menu-title {
        color: $sidebar-dark-menu-item-icon-color;
    }
}


body[data-layout="horizontal"] {
    .content-body {
        margin-left: 0 !important;
    }
}

// Compact Sidebar

body[data-sidebar-size="small"] {
    .brand-logo{
        width: $sidebar-width-sm;
        @media (max-width: 991.98px){
            width: auto;
        }
    }
    .left-panel{
        width: $sidebar-width-sm;
        text-align: center;

        .menu-arrow:after,
        .badge {
            display: none !important;
        }

        .left-panel-btn{
            display: none;
        }
        
    }
    
    .content-body {
        margin-left: $sidebar-width-sm;
    }
    #topbar, .footer {
        left: $sidebar-width-sm;
        @media (max-width: 991.98px){
            left: 0;
        }
    }

    #main-menu {
        ul li {
            a{
                i{
                    display: block;
                }
            }
            ul.submenu-content {
                li {
                    a{
                        padding-left: 1.5rem;
                    }

                    ul.submenu-content li a {
                        padding-left: 1.5rem;
                    }
                }
            }
        }
    }

    &[data-sidebar="dark"] {
        #main-menu {
            ul li {
                &.main-menu-title{
                    background-color: lighten($sidebar-dark-bg, 2%);
                }
            }
        }
    }

    &:not(.vertical-collpsed) {
        .nav-header .left-panel-btn{
            display: block;
        }
    }
    &.vertical-collpsed {
        .content-body {
            margin-left: $sidebar-collapsed-width;
        }
        .left-panel {
            #main-menu{
                text-align: left;
                >ul{
                    >li{
                        >a {
                            i{
                                display: inline-block;
                            }
                        }
                    }
                }
            }
        }
        #topbar, .footer {
            left: $sidebar-collapsed-width;
        }
    }
}

// colored sidebar

body[data-sidebar="colored"] {
    .left-panel{
        background-color: $primary;

        .left-panel-btn{
            color: rgba($white, 0.8);
        }
    }
    .brand-logo{
        background-color: $primary;
    }

    .mm-active {
        color: $white !important;
        > a {
            background-color: rgba($white, 0.1);
            color: $white !important;
            >i{
                color: $white !important;   
            }
        }
        > i, .active {
            color: $white !important;
        }
    }

    #main-menu {
        ul {
            li {
                &.main-menu-title{
                    color: rgba($white, 0.7);
                }

                a{
                    color: rgba($white, 0.7);
                    i{
                        color: rgba($white, 0.7);
                    }
                }

                ul.submenu-content {
                    li {
                        a{
                            color: rgba($white, 0.6);
                        }
                    }
                }

                .badge{
                    &.badge-primary{
                        background-color: $white;
                        color: $primary;
                    }
                }
            }
        }
    }

    &.vertical-collpsed {
        .left-panel {
            #main-menu{
                >ul{
                    >li{
                        &:hover>a{
                            background-color: lighten($primary, 2%);
                            color: $white;
                            i{
                                color: $white;
                            }
                        }
                    }
                }

                ul{
                    li{
                        &.mm-active {
                            .active{
                                color: $main-menu-item-active-color !important;
                            }
                        }
                    }
                }
            }
        }
    }
}
