// 
// _header.scss
// 

#topbar {
    position: fixed;
    top: 0;
    right: 0;
    left: $sidebar-width;
    z-index: 1;
    background-color: $header-bg;
    box-shadow: $box-shadow;
    transition: all .2s ease-out;
}

.nav-header {
    display: flex;
    -ms-flex-pack: justify;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto;
    height: $header-height;
    padding: 0 calc(#{$grid-gutter-width} / 2) 0 0;

    .dropdown .show {
        &.nav-item {
            background-color: $gray-100;
        }
    }

    .brand-logo{
        display: none;
    }
}

.noti-all {
    padding: 12px;
    display: block;
}

body {
    &.vertical-collpsed {
        #topbar {
            left: $sidebar-collapsed-width;
            @media (max-width: 991.98px){
                left: 0;
            }
        }
    }
}

.brand-logo {
    padding: 0 1.2rem;
    width: $brand-logo-width;
    position: fixed;
    z-index: 1;
    transition: all .2s ease-out;
}

.logo {
    line-height: 70px;

    .logo-sm {
        display: none;
    }
}

/* Menu Button */

.left-panel-btn {
    padding: 0 1.1rem 0 1.1rem;

    .lines {
        width: 20px;
        display: block;
        position: relative;
        height: 13px;
        margin: 0 auto;
        transition: all .5s ease;
    }
    span {
        height: 2px;
        width: 100%;
        background-color: $nav-item-color;
        display: block;
        margin-bottom: 4px;
        -webkit-transition: -webkit-transform .5s ease;
        transition: -webkit-transform .5s ease;
        transition: transform .5s ease;
    }
}
.sidebar-enable {
    .left-panel-btn {
        span {
            position: absolute;
            &:first-child {
                top: 6px;
                -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
            }
            &:nth-child(2) {
                visibility: hidden;
            }
            &:last-child {
                width: 100%;
                top: 6px;
                -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
            }
        }
    }
}

/* Search */

.header-search {
    padding: calc(#{$header-height - 38px} / 2) 0;

    .form-control {
        border: 1px solid $topbar-search-border;
        height: 38px;
        padding-left: 40px;
        padding-right: 20px;
        background-color: $topbar-search-bg;
        box-shadow: none;
        border-radius: 12rem;
        width: 325px;
    }
    span {
        position: absolute;
        z-index: 10;
        font-size: 16px;
        line-height: 38px;
        left: 13px;
        top: 0;
        color: $gray-600;
    }
}

// Mega menu

.megamain-menu {
    li{
        position: relative;
        padding: 5px 0px;
        a{
            color: $dropdown-color;
        }
    }
}

@media (max-width: 991.98px) {
    .brand-logo {
        width: auto;
    }

    #topbar {
        left: 0;
        z-index: 1006;

        .nav-header {
        
            .brand-logo{
                display: inline-block;
                position: relative;
            }

            .left-panel-btn {
                display: inline-block;
            }
        }
    }

    .logo {

        span.logo-lg {
            display: none;
        }

        span.logo-sm {
            display: inline-block;
        }
    }
}

.content {
    padding: calc(#{$header-height} + #{$grid-gutter-width}) calc(#{$grid-gutter-width} / 2) $footer-height calc(#{$grid-gutter-width} / 2);
}

.nav-item {
    height: $header-height;
    box-shadow: none !important;
    color: $nav-item-color;
    border: 0;
    border-radius: 0px;

    &:hover {
        color: $nav-item-color;
    }
}

.nav-user {
    height: 34px;
    width: 34px;
}

.nav-icon {
    i {
        font-size: 20px;
        color: $nav-item-color;
    }

    .badge {
        position: absolute;
        top: 14px;
        right: 7px;
    }
}

.notify-item {
    .d-flex {
        padding: 0.75rem 1rem;
        display: flex;

        &:hover {
            background-color: $dropdown-link-hover-bg;
        }
    }
}

.noti-title {
    padding: 15px 20px;
}

.noti-symbol {
    width: 2.45rem;
    height: 2.45rem;
}

// Dropdown with Icons
.dropdown-icon-item {
    display: block;
    border-radius: 3px;
    line-height: 34px;
    text-align: center;
    padding: 15px 0 9px;
    display: block;
    border: 1px solid transparent;
    color: $gray-600;

    img {
        height: 24px;
    }

    span {
        display: block;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }

    &:hover {
        border-color: $gray-200;
    }
}

// Full Screen
.fullscreen-enable {
    [data-bs-toggle="fullscreen"] {
        .uil-minus-path::before {
            content: '\eb8d';
        }
    }
}

body[data-topbar="dark"] {
    #topbar { 
        background-color: $header-dark-bg;
    }
    .nav-header {
        .dropdown .show {
            &.nav-item {
                background-color: rgba($white, 0.05);
            }
        }
    }

    .nav-item {
        color: $header-dark-item-color;
    
        &:hover {
            color: $header-dark-item-color;
        }
    }

    .nav-user {
        background-color: rgba($white, 0.25);
    }
    
    .nav-icon {
        i {
            color: $header-dark-item-color;
        }
    }

    .header-search {
    
        .form-control {
            background-color: lighten($header-dark-bg,4%);
            color: $white;
        }
        span,
        input.form-control::-webkit-input-placeholder {
            color: rgba($white,0.5);
        }
    }
}

body[data-sidebar="dark"] {
    .brand-logo {
        background: $sidebar-dark-bg;
    }


}

@media (max-width: 600px) {
    .nav-header {
        .dropdown {
            position: static;

            .dropdown-menu {
                left: 10px !important;
                right: 10px !important;
            }
        }
    }
}

@media (max-width: 380px) {
    .brand-logo {
        display: none;
    }
}

body[data-layout="horizontal"] {
    .content {
        margin-top: $header-height;
        padding: calc(80px + #{$grid-gutter-width}) calc(#{$grid-gutter-width} / 2) $footer-height calc(#{$grid-gutter-width} / 2);

        @media (max-width: 991.98px) { 
            margin-top: 0px;
        }
    }    
}



@media (max-width: 374.99px) { 
    .brand-logo {
        padding: 0 1rem;
        text-align: center;
    }

    .language-switch {
        display: none !important;
    }
}