// 
// authentication.scss
//


// authentication

.authentication-bg{
    background-color: #fff;
    .auth-logo{
        .logo{
            margin: 0px auto;
        }
    }

}
.inputcard {
    background: #fff;
    border: 1px solid #C2C2C2;
    padding: 10px;
    border-radius: 4px;
    margin: 12px 0px 10px 0px;
}
.inputcard input {
    border: 0;
    box-shadow: none;
    outline: none;
    padding: 0px 9px;
    font-size: 16px;
    font-weight: 400;
}
.inputcard input:focus {
    box-shadow: none;
    border: 0;
    outline: none;
}
.coth {
    border-left: 1px solid #C3C3C3;
}
.coth input {
    width: 80px;
}
.cnum {
    width: calc(100% - 160px)
}
.authtitle {
    font-size: 32px;
    font-weight: 500;
}
.packtitle {
    font-size: 21px;
    font-weight: 600;
    text-align: center;
}
.packprice {
    font-size: 35px;
    font-weight: 300;
    text-align: center;
    color: #268d61;
}
.auth-left {
    position: fixed;
    width: 40%;
    height: 100vh;
    padding: 30px;
    background: #464646;
    display: flex;
    align-items: center;
    justify-content: center;
}
.auth-right {
    margin-left: 40%;
    min-height: 100vh;
    padding: 2% 8%;
    display: flex;
    align-items: center;
}
.plan-taggle {
    background: #268d6140;
    border-radius: 40px;
    padding: 5px;
    display: inline-block;
}
.plan-taggle .btn {
    border-radius: 20px;
    font-size: 17px;
    font-weight: 500;
    padding-left: 15px;
    padding-right: 16px;
}
.plan-taggle .btn.active {
    background: #268d61;
    color: #fff;
    min-width: 110px;
}

.auth-right .form-control {
    padding: 0.6rem 0.75rem;
    font-size: 1rem;
}
.addcard {
    width: 400px;
    background: #F0F0F0;
    padding: 16px 13px;
    margin: 0 auto;
    border-radius: 5px;
}
.authlefttext {
    font-size: 35px;
    font-weight: 500;
    color: #fff;
    margin-bottom: 50px
}

// signin card title

.signin-other-title{
    position: relative;
    &:after{
        content: "";
        position: absolute;
        width: 100%;
        height: 1px;
        left: 0;
        right: 0;
        background-color: $border-color;
        top: 10px;
    }

    .title{
        display: inline-block;
        position: relative;
        z-index: 9;
        background-color: $card-bg;
        padding: 2px 16px;
    }
}


// auth carousel

.auth-carousel{
    &.owl-theme {
        .owl-dots {
            .owl-dot {
                span{
                    background-color: $card-bg;
                }
                &.active{
                    span{
                        background-color: $primary;
                    }
                }
            }
        }
    }
}
@media (min-width: 768px) and (max-width: 1024px) {
    .authlefttext {
        font-size: 23px;
    }
}
@media (max-width: 767px) {
    .auth-left {
        display: none;
    }
    .auth-right {
        margin-left: 0px;
    }
}